





















































#article h2{
  font-weight: normal;
}

#article{
  padding: 2em;
  font-size: 17px;
  line-height: 2em;
}

#article .title{
  font-size: 2em;
  border-bottom: 1px solid gainsboro;
  padding-bottom: .4em;
}

#article a{
  color: #999;
  word-wrap: break-word;
}

#article a:hover{
  color: #2B2B2B;
}

#words{
  width: 100%;
  text-align: center;
  margin-top: 2em;
  font-size: 16px;
}


code{
  font-size: 12px;
  line-height: 1.5em;
}

@import "../assets/monokai_sublime.css";




